
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Scroller,
Divider
},
props: {
height: {
type: String,
default: () => {
return ''
}
},
condition: {
type: Object,
default: () => {
return {
pageNum: 1,
pageSize: 10
}
}
}
},
data() {
return {
pullupConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
pulldownConfig: {
content: '',
downContent: '',
upContent: '...',
loadingContent: '...'
},
isNoMore: false
}
},
methods: {
refresh() {
setTimeout(() => {
this.$set(this.condition, 'pageNum', 1)
this.$emit('on-pulldown-loading', this.condition)
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.$refs.scroller.enablePullup()
this.isNoMore = false
}, 10)
})
}, 1000)
},
loadMore() {
this.$set(this.condition, 'pageNum', this.condition.pageNum + 1)
this.$emit('on-pullup-loading', this.condition)
},
setNoMoreState(value) {
if (value === true) this.$refs.scroller.disablePullup()
this.$set(this, 'isNoMore', value)
}
}
}



